.p-datatable {
  font-size: 10px !important;
  .p-datatable-header {
    padding: 10px !important;
  }
  .p-inputtext {
    font-size: 12px;
    padding: 5px 5px 5px 40px;
  }
  .p-sortable-column {
    .p-sortable-column-icon {
      font-size: 12px;
    }
  }
  .p-datatable-thead {
    tr {
      th {
        padding: 6px;
      }
    }
  }
  .p-datatable-tbody {
    tr {
      td {
        padding: 6px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 150px;
      }
    }
  }
  .p-paginator {
    .p-paginator-page,
    .p-paginator-first,
    .p-paginator-prev,
    .p-paginator-next,
    .p-paginator-last {
      min-width: 30px;
      height: 30px;
    }
  }
}

tr:has(td div.revenueInd.blue) {
  background-color: #7cb5ec;
}
tr:has(td div.revenueInd.green) {
  background-color: #90ed7d;
}
tr:has(td div.revenueInd.yellow) {
  background-color: #e4d354;
}
tr:has(td div.revenueInd.orange) {
  background-color: #f7a35c;
}
tr:has(td div.revenueInd.brown-red) {
  background-color: #e5a4a4;
}

.right-align {
  text-align: right;
}
